<template>
  <div class="container">
    <h3>Employee Database</h3>

    <form>
      <div class="input-group" v-show="showTable">
        <div class="col-md-12">
          <h2>Please use the search box to search by name!</h2>
        </div>
        <div class="col-md-6">
          <input
            type="search"
            class="form-control rounded"
            placeholder="First Name"
            aria-label="Search"
            aria-describedby="search-addon"
            ref="groupId1"
            v-model="firstName"
          />
        </div>
        <div class="col-md-6">
          <input
            type="search"
            class="form-control rounded"
            placeholder="Last Name"
            aria-label="Search"
            aria-describedby="search-addon"
            ref="groupId2"
            v-model="lastName"
          />
        </div>
        <div class="col-md-12">
          <button
            type="button"
            class="btn btn-outline-primary"
            v-on:click="searchText()"
          >
            Search
          </button>
        </div>
      </div>
    </form>

    <div v-show="showTable">
      <h2>Employees in the database are</h2>
      <table class="table">
        <thead>
          <tr>
            <th scope="col">ID</th>
            <th scope="col">First Name</th>
            <th scope="col">Last Name</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="user in users" v-bind:key="user.id">
            <th scope="row">{{ user.id }}</th>
            <td>{{ user.firstName }}</td>
            <td>{{ user.lastName }}</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div v-show="!showTable">
      <h2>Search Results are:</h2>
      <table class="table">
        <thead>
          <tr>
            <th scope="col">ID</th>
            <th scope="col">First Name</th>
            <th scope="col">Last Name</th>
            <th scope="col">Address</th>
            <th scope="col">Date of joining</th>
            <th scope="col">Contact Numbers</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="user in users" v-bind:key="user.id">
            <th scope="row">{{ user.id }}</th>
            <td>{{ user.firstName }}</td>
            <td>{{ user.lastName }}</td>
            <td>{{ user.address }}</td>
            <td>{{ user.dateOfJoining }}</td>
            <td>
              {{
                user.numbers.map((item) => item.type + ":" + item.phoneNumber).join(", ")
              }}
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
import axios from "axios";

export default {
  name: "Users",

  data() {
    return {
      users: null,
      showTable: true,
    };
  },
  created: function () {
    axios.get("http://localhost:8080/api/employees").then((res) => {
      this.users = res.data;
    });
  },
  methods: {
    searchText() {
      axios
        .get(
          "http://localhost:8080/api/employees/" +
            this.$refs.groupId1.value +
            "/" +
            this.$refs.groupId2.value
        )
        .then((res) => {
          this.users = res.data;
        });
      this.showTable = false;
    },
  },
};
</script>
<style>
h3 {
  margin-bottom: 5%;
}
div {
  padding: 0.5%;
}
</style>